.react-tabs__tab-list {
    border-bottom: 1px solid rgb(224, 224, 224);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-left: 50%;
    width: 90%;
    float:left;
    max-width: 1300px;
    transform: translateX(-50%);
}

.react-tabs__tab--selected {
    color:#7600FD;
    border: none;
    font-weight: 600;
    border-bottom: 2px solid #7600fd;
}
.react-tabs__tab {
    margin-right: 20px;
}