.headBody1 {
    float: left;
    width: 100%;
    padding-bottom: 15px;
    border-bottom: 1px solid rgb(224, 224, 224);
}
.headUpNavMain {
    float: left;
    width: 100%;
    margin-left: 50%;
    transform: translateX(-50%);
    position: relative;
}
.headUpNavMainT {
    float: left;
    width: 100%;
    margin-left: 50%;
    transform: translateX(-50%);
    position: relative;
    height: 150px;
    background-color: white;
}
.headUpNav {
    float: right;
    width: auto;
    margin-top: 16px;
    margin-right: 50px;
}
.headUpNavMain1 {
    float: left;
    width: 30px;
    margin-top: 25px;
    margin-left: 20px;
}
.headUpNavMain1 img {
    width: 100%;
}
.headUpNavLink {
    text-decoration: none;
    color: rgb(255, 255, 255);
    font-family: calibri;
    font-weight: 500;
}
.headUpNavLink span {
    padding-top: 5px;
    margin-right: 20px;
    color: rgb(0, 0, 0);
}
.headUpNav a {
    float: left;
    padding-top: 4px;
}
.headUpNav a span {
    border-radius: 50px;
    padding: 5px 12px;
    font-size: 14px;
    padding-bottom: 7px;
    color: rgb(0, 0, 0);
    font-weight: 600;
    margin-right: 5px;
}
.headUpNav a span:hover {
    background-color: rgba(193, 193, 193, 0.217) !important;
}
.headUpNav a img {
    width: 20px;
    padding: 5px;
    border-radius: 50px;
    cursor: pointer;
}
.headUpNav a img:hover {
    background-color: rgba(193, 193, 193, 0.347) !important;
}
.headUpNav span {
    cursor: pointer;
    margin-left: 15px;
    font-weight: 600;
    margin-top: 2px;
    float: left;
}
.headUpNav span img {
    width: 60px;
    float: left;
}

.headNavE {
    float: left;
    width: 400px;
    padding: 2px 20px;
    border-radius: 50px;
    margin-top: 14px;
    position: absolute;
    margin-left: 50%;
    transform: translateX(-50%);
    background-color: white;
    border: 1px solid rgb(223, 223, 223);
    cursor: pointer;
}
.headNavET {
    float: left;
    width: 550px;
    padding: 2px 0;
    border-radius: 50px;
    margin-top: 60px;
    position: absolute;
    margin-left: 50%;
    transform: translateX(-50%);
    background-color: white;
    border: 1px solid rgb(223, 223, 223);
    cursor: pointer;
}
.headNavE:hover {
    box-shadow: 0 2px 3px rgb(230, 230, 230);
}
.headNavEMinCarList {
    float: left;
    width: 400px;
