.headSideBar {
    background-color: white;
    position: absolute;
    width: 200px;
    top: 40px;
    border: 1px solid rgb(199, 199, 199);
    right: 20px;
    border-radius: 6px;
    padding: 10px 0;
}
.headSideBar p {
    padding: 6px 12px;
    cursor: pointer;
    font-family: calibri;
}
.headSideBar p:hover {
    background-color: rgba(232, 232, 238, 0.585);
    font-weight: 600;
}
hr {
    background-color: rgb(233, 233, 233);
}
.headNav0 input {
